import React from "react";
import Head from "next/head";
import Proptypes from "prop-types";
import withRedux from "next-redux-wrapper";
import withReduxSaga from "next-redux-saga";
import { createStore, compose, applyMiddleware } from "redux";
import { Provider } from "react-redux";
import createSagaMiddleware from "redux-saga";
import axios from "axios";
import Helmet from "react-helmet";
// import App from 'next/app';

import AppLayout from "../components/AppLayout";
import reducer from "../reducers";
import rootSaga from "../sagas";
import { LOAD_USER_REQUEST } from "../reducers/user";

const Nodebird = ({ Component, store, pageProps }) => {
    // console.log(pageProps);
    return (
        <Provider store={store}>
            <Helmet
                title="NodeBird"
                htmlAttributes={{ lang: "ko" }}
                meta={[
                    {
                        charset: "UTF-8"
                    },
                    {
                        name: "viewport",
                        content:
                            "width=device-width,initial-scale=1.0,minimum-scale=1.0,maximum-scale=1.0,user-scalable=yes,viewport-fit=cover"
                    },
                    {
                        "http-equiv": "X-UA-Compatible",
                        content: "IE=edge"
                    },
                    {
                        name: "description",
                        content: " NodeBird SNS"
                    },
                    {
                        name: "og:title",
                        content: "NodeBird"
                    },
                    {
                        name: "og:description",
                        content: " NodeBird SNS"
                    },
                    {
                        property: "og:type",
                        content: "website"
                    }
                ]}
                link={[
                    {
                        rel: "shortcut icon",
                        href: "/favicon.ico"
                    },
                    {
                        rel: "stylesheet",
                        href:
                            "https://cdnjs.cloudflare.com/ajax/libs/antd/3.25.3/antd.css"
                    },
                    {
                        rel: "stylesheet",
                        href:
                            "https://cdnjs.cloudflare.com/ajax/libs/slick-carousel/1.6.0/slick.min.css"
                    },
                    {
                        rel: "stylesheet",
                        href:
                            "https://cdnjs.cloudflare.com/ajax/libs/slick-carousel/1.6.0/slick-theme.min.css"
                    }
                ]}
            />

            <AppLayout>
                <Component {...pageProps} />
            </AppLayout>
        </Provider>
    );
};

Nodebird.propTypes = {
    Component: Proptypes.elementType.isRequired,
    store: Proptypes.object.isRequired,
    pageProps: Proptypes.object.isRequired
};
// getInitialProps
// next  react   
//        .
// react componentDidMount   .
//  .
//       .
Nodebird.getInitialProps = async context => {
    //context -> next  
    // console.log(context);
    const { ctx, Component } = context;
    let pageProps = {};
    // store     getState     .
    const state = ctx.store.getState();
    //  ->       
    //     ex) ( ) -  
    //     .
    const cookie = ctx.isServer ? ctx.req.headers.cookie : "";
    // console.log('', cookie);
    // axios        
    //
    if (ctx.isServer && cookie) {
        axios.defaults.headers.Cookie = cookie;
    }
    if (!state.user.me) {
        ctx.store.dispatch({
            type: LOAD_USER_REQUEST
        });
    }
    if (Component.getInitialProps) {
        // Component getInitialProps   
        pageProps = (await Component.getInitialProps(ctx)) || {}; // Component  props ()
    }
    return { pageProps };
};

const configStore = (initialState, options) => {
    const sagaMiddleware = createSagaMiddleware();
    const middlewares = [
        sagaMiddleware,
        store => next => action => {
            // console.log(action);
            next(action);
        }
    ];
    const enhancer =
        process.env.NODE_ENV === "production"
            ? compose(applyMiddleware(...middlewares))
            : compose(
                  // middleware 
                  applyMiddleware(...middlewares),
                  !options.isServer &&
                      window.__REDUX_DEVTOOLS_EXTENSION__ !== "undefined" // chrome redux devtools  
                      ? window.__REDUX_DEVTOOLS_EXTENSION__()
                      : f => f
              );
    const store = createStore(reducer, initialState, enhancer);
    // next redux-saga      
    store.sagaTask = sagaMiddleware.run(rootSaga);
    return store;
};

// next-redux-saga -> next  redux-saga     
// next-redux-saga       export  ( )

//  
export default withRedux(configStore)(withReduxSaga(Nodebird));
